import { Vehicle, VehicleType, RateCard, ParkingReceipt } from '@/types/parking';

export const GST_RATE = 0.09; // 9% GST

export const RATE_CARD: RateCard[] = [
  { vehicleType: '2-wheeler', hourlyRate: 25, dailyRate: 150 },
  { vehicleType: '4-wheeler', hourlyRate: 50, dailyRate: 300 },
  { vehicleType: 'bus', hourlyRate: 120, dailyRate: 600 },
  { vehicleType: 'truck', hourlyRate: 100, dailyRate: 500 },
];

export function calculateParkingCharges(
  vehicleType: VehicleType,
  entryTime: Date,
  exitTime: Date
): { baseAmount: number; gstAmount: number; totalAmount: number; duration: string } {
  const rateInfo = RATE_CARD.find(rate => rate.vehicleType === vehicleType);
  if (!rateInfo) {
    throw new Error('Invalid vehicle type');
  }

  const durationMs = exitTime.getTime() - entryTime.getTime();
  const durationHours = Math.ceil(durationMs / (1000 * 60 * 60));
  const durationDays = Math.floor(durationHours / 24);
  const remainingHours = durationHours % 24;

  let baseAmount = 0;
  let duration = '';

  if (durationDays > 0) {
    baseAmount = durationDays * rateInfo.dailyRate;
    if (remainingHours > 0) {
      baseAmount += remainingHours * rateInfo.hourlyRate;
    }
    duration = `${durationDays} day(s)${remainingHours > 0 ? ` ${remainingHours} hour(s)` : ''}`;
  } else {
    baseAmount = durationHours * rateInfo.hourlyRate;
    duration = `${durationHours} hour(s)`;
  }

  const gstAmount = Math.round(baseAmount * GST_RATE);
  const totalAmount = baseAmount + gstAmount;

  return { baseAmount, gstAmount, totalAmount, duration };
}

export function generateReceiptNumber(): string {
  const now = new Date();
  const timestamp = now.getTime().toString().slice(-6);
  return `ISKCON-${timestamp}`;
}

export function formatCurrency(amount: number): string {
  return `₹${amount.toFixed(2)}`;
}

export function formatDateTime(date: Date): string {
  return date.toLocaleString('en-IN', {
    day: '2-digit',
    month: '2-digit',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
    hour12: true
  });
}

export function generateParkingSlots(): Array<{ id: string; slotNumber: string; status: 'available' | 'occupied' }> {
  const slots = [];
  
  // 2-wheeler slots (A1-A20)
  for (let i = 1; i <= 20; i++) {
    slots.push({
      id: `2w-${i}`,
      slotNumber: `A${i}`,
      status: 'available' as const
    });
  }
  
  // 4-wheeler slots (B1-B30)
  for (let i = 1; i <= 30; i++) {
    slots.push({
      id: `4w-${i}`,
      slotNumber: `B${i}`,
      status: 'available' as const
    });
  }
  
  // Bus/Truck slots (C1-C10)
  for (let i = 1; i <= 10; i++) {
    slots.push({
      id: `heavy-${i}`,
      slotNumber: `C${i}`,
      status: 'available' as const
    });
  }
  
  return slots;
}

export function getAvailableSlot(vehicleType: VehicleType, occupiedSlots: string[]): string | null {
  let prefix = '';
  let range = 0;
  
  switch (vehicleType) {
    case '2-wheeler':
      prefix = 'A';
      range = 20;
      break;
    case '4-wheeler':
      prefix = 'B';
      range = 30;
      break;
    case 'bus':
    case 'truck':
      prefix = 'C';
      range = 10;
      break;
  }
  
  for (let i = 1; i <= range; i++) {
    const slotNumber = `${prefix}${i}`;
    if (!occupiedSlots.includes(slotNumber)) {
      return slotNumber;
    }
  }
  
  return null;
}
