'use client';

import { ParkingSlot } from '@/types/parking';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';

interface ParkingSlotsProps {
  parkingSlots: ParkingSlot[];
}

export default function ParkingSlots({ parkingSlots }: ParkingSlotsProps) {
  const twoWheelerSlots = parkingSlots.filter(slot => slot.slotNumber.startsWith('A'));
  const fourWheelerSlots = parkingSlots.filter(slot => slot.slotNumber.startsWith('B'));
  const heavyVehicleSlots = parkingSlots.filter(slot => slot.slotNumber.startsWith('C'));

  const SlotGrid = ({ slots, title, color }: { slots: ParkingSlot[], title: string, color: string }) => (
    <Card className={`border-${color}-200`}>
      <CardHeader>
        <CardTitle className={`text-${color}-800`}>{title}</CardTitle>
        <CardDescription>
          {slots.filter(s => s.status === 'available').length} available, {slots.filter(s => s.status === 'occupied').length} occupied
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-5 gap-2">
          {slots.map((slot) => (
            <div
              key={slot.id}
              className={`
                relative p-3 rounded-lg border-2 text-center text-sm font-medium transition-all
                ${slot.status === 'available' 
                  ? `bg-green-50 border-green-200 text-green-800 hover:bg-green-100` 
                  : `bg-red-50 border-red-200 text-red-800`
                }
              `}
            >
              <div className="font-bold">{slot.slotNumber}</div>
              {slot.status === 'occupied' && slot.vehicleType && (
                <div className="text-xs mt-1">
                  <Badge variant="secondary" className="text-xs">
                    {slot.vehicleType}
                  </Badge>
                </div>
              )}
              <div className={`
                absolute top-1 right-1 w-2 h-2 rounded-full
                ${slot.status === 'available' ? 'bg-green-400' : 'bg-red-400'}
              `} />
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );

  const stats = {
    total: parkingSlots.length,
    available: parkingSlots.filter(s => s.status === 'available').length,
    occupied: parkingSlots.filter(s => s.status === 'occupied').length,
    twoWheeler: {
      total: twoWheelerSlots.length,
      available: twoWheelerSlots.filter(s => s.status === 'available').length,
      occupied: twoWheelerSlots.filter(s => s.status === 'occupied').length,
    },
    fourWheeler: {
      total: fourWheelerSlots.length,
      available: fourWheelerSlots.filter(s => s.status === 'available').length,
      occupied: fourWheelerSlots.filter(s => s.status === 'occupied').length,
    },
    heavy: {
      total: heavyVehicleSlots.length,
      available: heavyVehicleSlots.filter(s => s.status === 'available').length,
      occupied: heavyVehicleSlots.filter(s => s.status === 'occupied').length,
    }
  };

  return (
    <div className="space-y-6">
      {/* Overall Statistics */}
      <Card className="border-orange-200">
        <CardHeader>
          <CardTitle className="text-orange-800">Parking Overview</CardTitle>
          <CardDescription>Real-time parking slot status</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-blue-50 rounded-lg">
              <div className="text-2xl font-bold text-blue-600">{stats.total}</div>
              <div className="text-sm text-blue-800">Total Slots</div>
            </div>
            <div className="text-center p-4 bg-green-50 rounded-lg">
              <div className="text-2xl font-bold text-green-600">{stats.available}</div>
              <div className="text-sm text-green-800">Available</div>
            </div>
            <div className="text-center p-4 bg-red-50 rounded-lg">
              <div className="text-2xl font-bold text-red-600">{stats.occupied}</div>
              <div className="text-sm text-red-800">Occupied</div>
            </div>
            <div className="text-center p-4 bg-orange-50 rounded-lg">
              <div className="text-2xl font-bold text-orange-600">
                {Math.round((stats.occupied / stats.total) * 100)}%
              </div>
              <div className="text-sm text-orange-800">Occupancy</div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Legend */}
      <Card>
        <CardHeader>
          <CardTitle className="text-orange-800">Legend</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex flex-wrap gap-4">
            <div className="flex items-center gap-2">
              <div className="w-4 h-4 bg-green-400 rounded-full"></div>
              <span className="text-sm">Available</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-4 h-4 bg-red-400 rounded-full"></div>
              <span className="text-sm">Occupied</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="px-2 py-1 bg-blue-50 border border-blue-200 rounded text-xs">A1-A20</div>
              <span className="text-sm">2-Wheeler Slots</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="px-2 py-1 bg-green-50 border border-green-200 rounded text-xs">B1-B30</div>
              <span className="text-sm">4-Wheeler Slots</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="px-2 py-1 bg-purple-50 border border-purple-200 rounded text-xs">C1-C10</div>
              <span className="text-sm">Bus/Truck Slots</span>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Slot Grids */}
      <div className="space-y-6">
        <SlotGrid 
          slots={twoWheelerSlots} 
          title="🏍️ 2-Wheeler Parking (A1-A20)" 
          color="blue"
        />
        
        <SlotGrid 
          slots={fourWheelerSlots} 
          title="🚗 4-Wheeler Parking (B1-B30)" 
          color="green"
        />
        
        <SlotGrid 
          slots={heavyVehicleSlots} 
          title="🚌 Bus/Truck Parking (C1-C10)" 
          color="purple"
        />
      </div>

      {/* Detailed Statistics */}
      <Card>
        <CardHeader>
          <CardTitle className="text-orange-800">Detailed Statistics</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid md:grid-cols-3 gap-4">
            <div className="space-y-2">
              <h4 className="font-medium text-blue-800">2-Wheeler Section</h4>
              <div className="text-sm space-y-1">
                <div>Total: {stats.twoWheeler.total} slots</div>
                <div>Available: {stats.twoWheeler.available}</div>
                <div>Occupied: {stats.twoWheeler.occupied}</div>
                <div>Occupancy: {Math.round((stats.twoWheeler.occupied / stats.twoWheeler.total) * 100)}%</div>
              </div>
            </div>
            
            <div className="space-y-2">
              <h4 className="font-medium text-green-800">4-Wheeler Section</h4>
              <div className="text-sm space-y-1">
                <div>Total: {stats.fourWheeler.total} slots</div>
                <div>Available: {stats.fourWheeler.available}</div>
                <div>Occupied: {stats.fourWheeler.occupied}</div>
                <div>Occupancy: {Math.round((stats.fourWheeler.occupied / stats.fourWheeler.total) * 100)}%</div>
              </div>
            </div>
            
            <div className="space-y-2">
              <h4 className="font-medium text-purple-800">Heavy Vehicle Section</h4>
              <div className="text-sm space-y-1">
                <div>Total: {stats.heavy.total} slots</div>
                <div>Available: {stats.heavy.available}</div>
                <div>Occupied: {stats.heavy.occupied}</div>
                <div>Occupancy: {Math.round((stats.heavy.occupied / stats.heavy.total) * 100)}%</div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
