'use client';

import { useState } from 'react';
import { Vehicle, VehicleType } from '@/types/parking';
import { getAvailableSlot } from '@/lib/parking-utils';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface VehicleEntryFormProps {
  onVehicleEntry: (vehicle: Vehicle) => void;
  occupiedSlots: string[];
}

export default function VehicleEntryForm({ onVehicleEntry, occupiedSlots }: VehicleEntryFormProps) {
  const [vehicleNumber, setVehicleNumber] = useState('');
  const [vehicleType, setVehicleType] = useState<VehicleType | ''>('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setIsSubmitting(true);

    try {
      // Validation
      if (!vehicleNumber.trim()) {
        throw new Error('Vehicle number is required');
      }
      if (!vehicleType) {
        throw new Error('Vehicle type is required');
      }

      // Format vehicle number
      const formattedNumber = vehicleNumber.trim().toUpperCase();

      // Get available slot
      const availableSlot = getAvailableSlot(vehicleType, occupiedSlots);
      if (!availableSlot) {
        throw new Error(`No available slots for ${vehicleType}`);
      }

      // Create vehicle entry
      const vehicle: Vehicle = {
        id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        number: formattedNumber,
        type: vehicleType,
        entryTime: new Date(),
        slotNumber: availableSlot,
        baseAmount: 0,
        gstAmount: 0,
        totalAmount: 0,
        status: 'parked'
      };

      onVehicleEntry(vehicle);
      
      // Reset form
      setVehicleNumber('');
      setVehicleType('');
      setSuccess(`Vehicle ${formattedNumber} parked successfully in slot ${availableSlot}!`);
      
      // Clear success message after 3 seconds
      setTimeout(() => setSuccess(''), 3000);

    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setIsSubmitting(false);
    }
  };

  const getAvailableSlotCount = (type: VehicleType): number => {
    let totalSlots = 0;
    let prefix = '';
    
    switch (type) {
      case '2-wheeler':
        totalSlots = 20;
        prefix = 'A';
        break;
      case '4-wheeler':
        totalSlots = 30;
        prefix = 'B';
        break;
      case 'bus':
      case 'truck':
        totalSlots = 10;
        prefix = 'C';
        break;
    }
    
    const occupiedCount = occupiedSlots.filter(slot => slot.startsWith(prefix)).length;
    return totalSlots - occupiedCount;
  };

  return (
    <div className="max-w-md mx-auto">
      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="space-y-2">
          <Label htmlFor="vehicleNumber">Vehicle Number</Label>
          <Input
            id="vehicleNumber"
            type="text"
            placeholder="e.g., MH12AB1234"
            value={vehicleNumber}
            onChange={(e) => setVehicleNumber(e.target.value)}
            className="uppercase"
            disabled={isSubmitting}
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="vehicleType">Vehicle Type</Label>
          <Select value={vehicleType} onValueChange={(value: VehicleType) => setVehicleType(value)}>
            <SelectTrigger>
              <SelectValue placeholder="Select vehicle type" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="2-wheeler">
                2-Wheeler ({getAvailableSlotCount('2-wheeler')} slots available)
              </SelectItem>
              <SelectItem value="4-wheeler">
                4-Wheeler ({getAvailableSlotCount('4-wheeler')} slots available)
              </SelectItem>
              <SelectItem value="bus">
                Bus ({getAvailableSlotCount('bus')} slots available)
              </SelectItem>
              <SelectItem value="truck">
                Truck ({getAvailableSlotCount('truck')} slots available)
              </SelectItem>
            </SelectContent>
          </Select>
        </div>

        {vehicleType && (
          <Card className="bg-blue-50 border-blue-200">
            <CardContent className="pt-4">
              <div className="text-sm text-blue-800">
                <div className="font-medium mb-2">Parking Rates for {vehicleType}:</div>
                <div className="space-y-1">
                  {vehicleType === '2-wheeler' && (
                    <>
                      <div>Hourly: ₹10</div>
                      <div>Daily: ₹50</div>
                    </>
                  )}
                  {vehicleType === '4-wheeler' && (
                    <>
                      <div>Hourly: ₹20</div>
                      <div>Daily: ₹100</div>
                    </>
                  )}
                  {vehicleType === 'bus' && (
                    <>
                      <div>Hourly: ₹50</div>
                      <div>Daily: ₹250</div>
                    </>
                  )}
                  {vehicleType === 'truck' && (
                    <>
                      <div>Hourly: ₹40</div>
                      <div>Daily: ₹200</div>
                    </>
                  )}
                  <div className="text-xs mt-2 text-blue-600">
                    *GST (9%) will be added to the final amount
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {error && (
          <Alert className="border-red-200 bg-red-50">
            <AlertDescription className="text-red-800">{error}</AlertDescription>
          </Alert>
        )}

        {success && (
          <Alert className="border-green-200 bg-green-50">
            <AlertDescription className="text-green-800">{success}</AlertDescription>
          </Alert>
        )}

        <Button 
          type="submit" 
          className="w-full bg-orange-600 hover:bg-orange-700"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Processing...' : 'Park Vehicle'}
        </Button>
      </form>

      <div className="mt-6 p-4 bg-orange-50 rounded-lg">
        <h3 className="font-medium text-orange-800 mb-2">Parking Guidelines:</h3>
        <ul className="text-sm text-orange-700 space-y-1">
          <li>• Please ensure vehicle number is correct</li>
          <li>• Keep your parking receipt safe</li>
          <li>• GST (9%) is applicable on all parking charges</li>
          <li>• Daily rates apply for parking over 24 hours</li>
        </ul>
      </div>
    </div>
  );
}
