'use client';

import { useState } from 'react';
import { Vehicle } from '@/types/parking';
import { calculateParkingCharges, formatCurrency, formatDateTime, generateReceiptNumber } from '@/lib/parking-utils';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Separator } from '@/components/ui/separator';

interface BillingSystemProps {
  parkedVehicles: Vehicle[];
  onVehicleExit: (vehicleId: string, exitData: { exitTime: Date; baseAmount: number; gstAmount: number; totalAmount: number }) => void;
}

export default function BillingSystem({ parkedVehicles, onVehicleExit }: BillingSystemProps) {
  const [searchNumber, setSearchNumber] = useState('');
  const [selectedVehicle, setSelectedVehicle] = useState<Vehicle | null>(null);
  const [billingData, setBillingData] = useState<{
    baseAmount: number;
    gstAmount: number;
    totalAmount: number;
    duration: string;
    exitTime: Date;
  } | null>(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState('');
  const [showReceipt, setShowReceipt] = useState(false);

  const handleSearch = () => {
    setError('');
    const vehicle = parkedVehicles.find(v => 
      v.number.toLowerCase().includes(searchNumber.toLowerCase().trim())
    );
    
    if (!vehicle) {
      setError('Vehicle not found or already exited');
      setSelectedVehicle(null);
      setBillingData(null);
      return;
    }

    setSelectedVehicle(vehicle);
    
    // Calculate charges
    const exitTime = new Date();
    const charges = calculateParkingCharges(vehicle.type, vehicle.entryTime, exitTime);
    
    setBillingData({
      ...charges,
      exitTime
    });
  };

  const handleProcessExit = () => {
    if (!selectedVehicle || !billingData) return;
    
    setIsProcessing(true);
    
    try {
      onVehicleExit(selectedVehicle.id, {
        exitTime: billingData.exitTime,
        baseAmount: billingData.baseAmount,
        gstAmount: billingData.gstAmount,
        totalAmount: billingData.totalAmount
      });
      
      setShowReceipt(true);
      
      // Reset after showing receipt
      setTimeout(() => {
        setSearchNumber('');
        setSelectedVehicle(null);
        setBillingData(null);
        setShowReceipt(false);
      }, 5000);
      
    } catch (err) {
      setError('Failed to process vehicle exit');
    } finally {
      setIsProcessing(false);
    }
  };

  const handlePrintReceipt = () => {
    if (!selectedVehicle || !billingData) return;
    
    const receiptContent = `ISKCON TEMPLE PARKING RECEIPT
=============================

Receipt No: ${generateReceiptNumber()}
Date: ${formatDateTime(billingData.exitTime)}

Vehicle Details:
Number: ${selectedVehicle.number}
Type: ${selectedVehicle.type}
Slot: ${selectedVehicle.slotNumber}

Parking Duration:
Entry: ${formatDateTime(selectedVehicle.entryTime)}
Exit: ${formatDateTime(billingData.exitTime)}
Duration: ${billingData.duration}

Charges:
Base Amount: ${formatCurrency(billingData.baseAmount)}
GST (9%): ${formatCurrency(billingData.gstAmount)}
Total Amount: ${formatCurrency(billingData.totalAmount)}

Thank you for visiting ISKCON Temple!
Hare Krishna!

=============================`;
    
    const printWindow = window.open('', '_blank');
    
    if (!printWindow) {
      console.error('Unable to open print window. Please disable pop-up blockers and try again.');
      alert('Unable to open print window. Please disable pop-up blockers and try again.');
      return;
    }
    
    printWindow.document.write(`
      <html>
        <head>
          <title>Parking Receipt</title>
          <style>
            @page { 
              margin: 10mm; 
              size: A4;
            }
            body {
              font-family: 'Courier New', monospace;
              font-size: 12px;
              padding: 10px;
              max-width: 300px;
              margin: 0 auto;
              line-height: 1.3;
            }
            pre {
              white-space: pre-wrap;
              word-wrap: break-word;
              margin: 0;
              font-size: inherit;
            }
            @media print {
              body {
                font-size: 11px;
                max-width: 280px;
              }
            }
          </style>
        </head>
        <body onload="window.print(); window.close();">
          <pre>${receiptContent}</pre>
        </body>
      </html>
    `);
    printWindow.document.close();
  };

  if (showReceipt && selectedVehicle && billingData) {
    return (
      <div className="max-w-md mx-auto">
        <Card className="border-green-200 bg-green-50">
          <CardHeader className="text-center">
            <CardTitle className="text-green-800">🎉 Payment Successful!</CardTitle>
            <CardDescription>Vehicle exit processed successfully</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="text-center">
              <div className="text-2xl font-bold text-green-600">
                {formatCurrency(billingData.totalAmount)}
              </div>
              <div className="text-sm text-green-700">Total Amount Paid</div>
            </div>
            
            <Separator />
            
            <div className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span>Vehicle:</span>
                <span className="font-medium">{selectedVehicle.number}</span>
              </div>
              <div className="flex justify-between">
                <span>Duration:</span>
                <span>{billingData.duration}</span>
              </div>
              <div className="flex justify-between">
                <span>Slot:</span>
                <span>{selectedVehicle.slotNumber}</span>
              </div>
            </div>
            
            <Button 
              onClick={handlePrintReceipt}
              className="w-full bg-orange-600 hover:bg-orange-700"
            >
              Print Receipt
            </Button>
            
            <div className="text-center text-sm text-green-600">
              This window will close automatically in 5 seconds
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="max-w-2xl mx-auto space-y-6">
      {/* Search Section */}
      <Card>
        <CardHeader>
          <CardTitle className="text-orange-800">Find Vehicle</CardTitle>
          <CardDescription>Search by vehicle number</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex gap-2">
            <div className="flex-1">
              <Label htmlFor="search">Vehicle Number</Label>
              <Input
                id="search"
                type="text"
                placeholder="Enter vehicle number"
                value={searchNumber}
                onChange={(e) => setSearchNumber(e.target.value)}
                className="uppercase"
              />
            </div>
            <div className="flex items-end">
              <Button onClick={handleSearch} className="bg-orange-600 hover:bg-orange-700">
                Search
              </Button>
            </div>
          </div>
          
          {error && (
            <Alert className="mt-4 border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">{error}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>

      {/* Currently Parked Vehicles */}
      <Card>
        <CardHeader>
          <CardTitle className="text-orange-800">Currently Parked Vehicles</CardTitle>
          <CardDescription>Click on any vehicle to process exit</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-2 max-h-60 overflow-y-auto">
            {parkedVehicles.map((vehicle) => (
              <div
                key={vehicle.id}
                onClick={() => {
                  setSelectedVehicle(vehicle);
                  setSearchNumber(vehicle.number);
                  const exitTime = new Date();
                  const charges = calculateParkingCharges(vehicle.type, vehicle.entryTime, exitTime);
                  setBillingData({ ...charges, exitTime });
                }}
                className="flex justify-between items-center p-3 bg-gray-50 rounded-lg cursor-pointer hover:bg-gray-100 transition-colors"
              >
                <div>
                  <div className="font-medium">{vehicle.number}</div>
                  <div className="text-sm text-gray-600">
                    {vehicle.type} • Slot {vehicle.slotNumber}
                  </div>
                </div>
                <div className="text-right">
                  <div className="text-sm text-gray-600">
                    {formatDateTime(vehicle.entryTime)}
                  </div>
                </div>
              </div>
            ))}
            {parkedVehicles.length === 0 && (
              <div className="text-center text-gray-500 py-4">
                No vehicles currently parked
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Billing Details */}
      {selectedVehicle && billingData && (
        <Card className="border-orange-200">
          <CardHeader>
            <CardTitle className="text-orange-800">Billing Details</CardTitle>
            <CardDescription>GST calculation for {selectedVehicle.number}</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <h4 className="font-medium">Vehicle Information</h4>
                <div className="text-sm space-y-1">
                  <div>Number: <span className="font-medium">{selectedVehicle.number}</span></div>
                  <div>Type: <span className="font-medium">{selectedVehicle.type}</span></div>
                  <div>Slot: <span className="font-medium">{selectedVehicle.slotNumber}</span></div>
                </div>
              </div>
              
              <div className="space-y-2">
                <h4 className="font-medium">Parking Duration</h4>
                <div className="text-sm space-y-1">
                  <div>Entry: {formatDateTime(selectedVehicle.entryTime)}</div>
                  <div>Exit: {formatDateTime(billingData.exitTime)}</div>
                  <div>Duration: <span className="font-medium">{billingData.duration}</span></div>
                </div>
              </div>
            </div>
            
            <Separator />
            
            <div className="space-y-3">
              <h4 className="font-medium">Charge Breakdown</h4>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span>Base Parking Charges:</span>
                  <span>{formatCurrency(billingData.baseAmount)}</span>
                </div>
                <div className="flex justify-between text-sm text-gray-600">
                  <span>GST (9%):</span>
                  <span>{formatCurrency(billingData.gstAmount)}</span>
                </div>
                <Separator />
                <div className="flex justify-between text-lg font-bold text-orange-600">
                  <span>Total Amount:</span>
                  <span>{formatCurrency(billingData.totalAmount)}</span>
                </div>
              </div>
            </div>
            
            <Button 
              onClick={handleProcessExit}
              disabled={isProcessing}
              className="w-full bg-green-600 hover:bg-green-700"
            >
              {isProcessing ? 'Processing...' : 'Process Exit & Generate Receipt'}
            </Button>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
