'use client';

import { Vehicle, ParkingSlot } from '@/types/parking';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { formatCurrency, formatDateTime } from '@/lib/parking-utils';

interface ParkingDashboardProps {
  vehicles: Vehicle[];
  parkingSlots: ParkingSlot[];
  parkedVehicles: Vehicle[];
  exitedVehicles: Vehicle[];
}

export default function ParkingDashboard({ 
  vehicles, 
  parkingSlots, 
  parkedVehicles, 
  exitedVehicles 
}: ParkingDashboardProps) {
  const totalSlots = parkingSlots.length;
  const occupiedSlots = parkingSlots.filter(slot => slot.status === 'occupied').length;
  const availableSlots = totalSlots - occupiedSlots;
  
  const todayRevenue = exitedVehicles
    .filter(v => v.exitTime && new Date(v.exitTime).toDateString() === new Date().toDateString())
    .reduce((sum, v) => sum + v.totalAmount, 0);

  const vehicleTypeCount = {
    '2-wheeler': parkedVehicles.filter(v => v.type === '2-wheeler').length,
    '4-wheeler': parkedVehicles.filter(v => v.type === '4-wheeler').length,
    'bus': parkedVehicles.filter(v => v.type === 'bus').length,
    'truck': parkedVehicles.filter(v => v.type === 'truck').length,
  };

  return (
    <div className="space-y-6">
      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card className="border-green-200">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-green-800">Available Slots</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{availableSlots}</div>
            <p className="text-xs text-green-600">out of {totalSlots} total</p>
          </CardContent>
        </Card>

        <Card className="border-red-200">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-red-800">Occupied Slots</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{occupiedSlots}</div>
            <p className="text-xs text-red-600">currently parked</p>
          </CardContent>
        </Card>

        <Card className="border-blue-200">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-blue-800">Today's Vehicles</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{exitedVehicles.filter(v => v.exitTime && new Date(v.exitTime).toDateString() === new Date().toDateString()).length}</div>
            <p className="text-xs text-blue-600">completed parking</p>
          </CardContent>
        </Card>

        <Card className="border-orange-200">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-orange-800">Today's Revenue</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{formatCurrency(todayRevenue)}</div>
            <p className="text-xs text-orange-600">including GST</p>
          </CardContent>
        </Card>
      </div>

      {/* Vehicle Type Distribution */}
      <Card>
        <CardHeader>
          <CardTitle className="text-orange-800">Currently Parked Vehicles</CardTitle>
          <CardDescription>Distribution by vehicle type</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-blue-50 rounded-lg">
              <div className="text-2xl font-bold text-blue-600">{vehicleTypeCount['2-wheeler']}</div>
              <div className="text-sm text-blue-800">2-Wheelers</div>
            </div>
            <div className="text-center p-4 bg-green-50 rounded-lg">
              <div className="text-2xl font-bold text-green-600">{vehicleTypeCount['4-wheeler']}</div>
              <div className="text-sm text-green-800">4-Wheelers</div>
            </div>
            <div className="text-center p-4 bg-purple-50 rounded-lg">
              <div className="text-2xl font-bold text-purple-600">{vehicleTypeCount.bus}</div>
              <div className="text-sm text-purple-800">Buses</div>
            </div>
            <div className="text-center p-4 bg-yellow-50 rounded-lg">
              <div className="text-2xl font-bold text-yellow-600">{vehicleTypeCount.truck}</div>
              <div className="text-sm text-yellow-800">Trucks</div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Recent Activity */}
      <div className="grid md:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle className="text-orange-800">Recently Parked</CardTitle>
            <CardDescription>Latest vehicle entries</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {parkedVehicles.slice(-5).reverse().map((vehicle) => (
                <div key={vehicle.id} className="flex justify-between items-center p-3 bg-green-50 rounded-lg">
                  <div>
                    <div className="font-medium">{vehicle.number}</div>
                    <div className="text-sm text-gray-600">{vehicle.type} • Slot {vehicle.slotNumber}</div>
                  </div>
                  <div className="text-right">
                    <div className="text-sm text-gray-600">{formatDateTime(vehicle.entryTime)}</div>
                  </div>
                </div>
              ))}
              {parkedVehicles.length === 0 && (
                <div className="text-center text-gray-500 py-4">No vehicles currently parked</div>
              )}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="text-orange-800">Recent Exits</CardTitle>
            <CardDescription>Latest completed parkings</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {exitedVehicles.slice(-5).reverse().map((vehicle) => (
                <div key={vehicle.id} className="flex justify-between items-center p-3 bg-blue-50 rounded-lg">
                  <div>
                    <div className="font-medium">{vehicle.number}</div>
                    <div className="text-sm text-gray-600">{vehicle.type}</div>
                  </div>
                  <div className="text-right">
                    <div className="font-medium text-green-600">{formatCurrency(vehicle.totalAmount)}</div>
                    <div className="text-sm text-gray-600">
                      {vehicle.exitTime && formatDateTime(vehicle.exitTime)}
                    </div>
                  </div>
                </div>
              ))}
              {exitedVehicles.length === 0 && (
                <div className="text-center text-gray-500 py-4">No recent exits</div>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
